





































interface RuleFormValue {
  title: string;
  cate: string;
  description: string;
  createAt: number | string;
}
import formMixin from "@/mixins/form";
import {
  Component,
  Prop,
  Watch,
  Emit,
  Vue,
  Mixins
} from "vue-property-decorator";
@Component({})
export default class AddDialog extends Mixins(formMixin) {
  ruleForm: RuleFormValue = {
    title: "",
    cate: "",
    description: "",
    createAt: ""
  };
  rules: any = {
    title: [{ required: true, message: "请输入标题", trigger: "blur" }],
    cate: [{ required: true, message: "请输入类别", trigger: "blur" }],
    description: [{ required: true, message: "请输入描述", trigger: "change" }]
  };

  resetForm(formName) {
    (this as any).$refs[formName].resetFields();
  }

  async _saveData() {
    let res = await (this as any).$refs.ruleForm.validate();
    let query = {
      ...this.ruleForm
    };
    if (res) {
      console.log(this.$API.logModel.AddLog);
      return this.saveData(this.$API.logModel.AddLog, query);
    }
  }
}
